import React from 'react';
import { render } from 'react-dom';
import './style.css';
import Login from './login';

class XNBT extends React.Component {
  constructor() {
    super();
    this.state = {
      replaceIWindow: false,
      xmessagelist: [
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        ["Thanks for your time " + sessionStorage.getItem("firstName") + "!"]
      ],
      cursorvalue: "",
      continueShow: [true],
      umessagelist: [],
      boolarr: [
        false,
        false,
        false,
        false,
        false,
        false
      ],
      valarr: [
        [],
        [],
        [], 
        [], 
        [], 
        []
        ],
    }
  }

  componentDidMount = () => {

    //  curl -H 'x-origin:1' -H 'x-version:2' -X POST 'http://dev.xane.ai/auths/login' -d 'email=sahil.narain@gmail.com' -d 'password=password'

    fetch('http://dev.xane.ai/surveys/active?', {
      method: "GET",
      headers: {
        "X-Auth": "1088bf97af644bc29275b021da4c292c",
        "X-Origin": "1",
        "X-Version": "2"
      }
    })
      .then(res => res.json())
      .then((data) => {
          console.log(data);
          let xmessagelist = this.state.xmessagelist;
          xmessagelist[0].push(data.data.surveys[0].answered_questions[0].question);
          xmessagelist[1].push(data.data.surveys[0].answered_questions[1].question);
          xmessagelist[2].push(data.data.surveys[0].answered_questions[2].question);
          xmessagelist[3].push(data.data.surveys[0].answered_questions[3].question);
          xmessagelist[4].push(data.data.surveys[0].answered_questions[4].question);
          xmessagelist[5].push(data.data.surveys[0].answered_questions[5].question);
          xmessagelist[6].push(data.data.surveys[0].answered_questions[6].question);
          this.setState({xmessagelist: xmessagelist});
        })
      .catch(error => alert(error))
  }



  handleOption = (e) => {
    e.preventDefault();
    this.setState({ cursorvalue: e.target.value })
    let umessagelist = this.state.umessagelist;
    umessagelist.push(e.target.value);
    this.setState({ umessagelist: umessagelist });
    let continueshow = this.state.continueShow;
    continueshow.unshift(true);
    this.setState({ continueshow: continueshow });
    document.getElementById('BBubblebuttons').style.display = "none";
  }


  handleSubmit = (e) => {
    if (e.keyCode === 13) {
      if (e.target.value) {
        this.setState({ cursorvalue: e.target.value });
        let umessagelist = this.state.umessagelist;
        umessagelist.push(e.target.value);
        this.setState({ umessagelist: umessagelist });
        let continueshow = this.state.continueShow;
        continueshow.unshift(true);
        this.setState({ continueshow: continueshow });
        e.target.value = "";
      }
    }
  }

  handleToggle = () => {
    this.setState({ replaceIWindow: !this.state.replaceIWindow });
  }

  render() {
    if(sessionStorage.getItem("firstName"))
    {  
    if (this.state.replaceIWindow === false) {
      return (
        <div className="app">
          <NBar sendToggle={this.handleToggle} />
          <IWindow passx={this.state.xmessagelist} passu={this.state.umessagelist} showOption={this.state.boolarr} valarrpass={this.state.valarr} sendHandleOption={this.handleOption} usermessageshow={this.userMessageShow} cursorvalue={this.state.cursorvalue} continueShow={this.state.continueShow} />
          <TBar passSend={this.handleSubmit} />
        </div>
      )
    }

    else {
      return (
        <div className="app disableov">
          <NBar sendToggle={this.handleToggle} />
          <InfoScreen />
          <CopyBar />
        </div>
      )
    }
  }
  else{
    return(
      <Login />
    )
  }
  }
}

class NBar extends React.Component {

  render() {
    return (
      <div className="nbarformat">
        <Logo titletext="&nbsp;&nbsp;X" />
        <Logo titletext="ane" />
        <NButton sendToggle={this.props.sendToggle} />
      </div>
    )
  }
}

function Logo(props) {
  return (
    <h5 className="trim logoformat">{props.titletext}</h5>
  )
}

function NButton(props) {
  return (
    <button className="nbuttonformat" onClick={props.sendToggle}><i className="fas fa-info"></i></button>
  )
}

